*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body {
  line-height: 1.3;
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

li {
  list-style: none;
}

.values-section {
  margin-bottom: 5em;
}
.values-section .container {
  background: #f8f8f8;
}
.values-section .container .wrap {
  padding: 3em 0;
  width: 90%;
  margin: 0 auto;
}
.values-section .container .wrap .heading {
  text-align: center;
  font-weight: 500;
  text-transform: capitalize;
  margin-bottom: 2em;
}
.values-section .container .wrap .heading .acronym {
  text-transform: uppercase;
  color: #d6cd00;
  display: inline-block;
  margin: 0.3em 0 0.1em 0;
}
.values-section .container .wrap .heading .tagline {
  font-size: 0.7rem;
}
.values-section .container .wrap .value-wrap {
  margin-bottom: 2em;
  display: grid;
  gap: 10px;
}
.values-section .container .wrap .value-wrap .value {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  color: #6aba43;
}
.values-section .container .wrap .paragraph {
  max-width: 500px;
}
@media screen and (min-width: 43em) {
  .values-section .container .wrap .value-wrap {
    display: flex;
    gap: 20px;
    justify-content: center;
  }
  .values-section .container .wrap .value-wrap .value {
    display: inline-block;
  }
  .values-section .container .wrap .paragraph {
    margin: 0 auto;
    text-align: center;
  }
}